@use './../../styles/mixin';
@use './../../styles/vars';

.article {
  display: flex;
  flex-direction: column;
  font-weight: 500;

  @include mixin.paper-style(20px);
  @include mixin.paper-padding(30px 30px);

  &__image {
    position: relative;
    display: flex; // Добавляем flex для управления потомками
    flex-direction: column; // Располагаем потомков в колонку
    align-items: center; // Центрируем потомков по горизонтали
    justify-content: center; // Центрируем потомков по вертикали
    margin: auto;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 72.5%;

      background: rgba(78, 121, 185, 0.8);
      backdrop-filter: blur(4px);
      transition: 0.6s;
      border-radius: 10px;
      opacity: 0;
    }

    &:hover {
      &:before {
        opacity: 1;
      }

      .article__button {
        cursor: pointer;
        opacity: 1;
        transition: 0.4s;
      }
    }
  }

  &__button {
    display: flex;
    position: relative;
    top: -170px;
    left: 17%;

    font-weight: 500;
    color: #20a1ff;

    padding: 10px 25px;
    z-index: 2;

    border-radius: 6px;
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: 0.4s;
    border: 0 none;
    margin-bottom: 20px;
  }

  &__image {
    img {
      max-width: 100%;
      height: auto;
      border-radius: 10px;
      object-fit: cover;
      z-index: 1; 
    }
  }

  &__title {
    margin-top: -85px;

    font-weight: 500;
    font-size: 1.125rem;
    text-shadow: -1px 1px 1px rgb(20, 20, 20);
    letter-spacing: 1px;
    text-transform: uppercase;

    color: vars.$secondary-color;
  }

  &__description {
    margin-top: 10px;

    font-size: 1.7rem;
    text-shadow: -1px 1px 1px rgb(97, 15, 15);
    font-weight: 600;
    line-height: 34px;
    color: #1e2125;

    transition: 0.4s;
  }
}
